body, html, #root {
	height: 200vh;
	width: 100vw;
	background: rgb(37,72,94);
	background: linear-gradient(15deg, rgba(37,72,94,1) 0%, rgba(62,93,107,1) 42%, rgba(90,113,123,1) 100%);
	margin: 0;
	padding: 0;
	color: #ffffff;
	font-family: 'Comfortaa', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}



